import { pluginName } from './constants'
import apiMock from './plugin-api-mock'

let api = apiMock

const getPluginApi = () => {
  api = api || window.top.pluginApi(pluginName)
  return api
}

function resetApi () {
  api = undefined
}

export { getPluginApi as default, resetApi }



